@use '../../variable' as color;
@use '../../variable' as style;

.select-container {
  display: flex;
  flex-direction: column;
  width: 300px;
}

.label {
  margin-bottom: 0.5rem;
}

.select {
  padding: 0 0 0 1rem;
  height: 40px;
  border: 0px;
  border-bottom: 1px solid color.$dim-gray;
  transition: 0.3s ease-out;
  font-family: inherit;
  font-size: 16px;


  &:hover,
  &:focus {
    border-bottom: 1px solid color.$secondary-color;
  }
}